.footer {
    display: flex;
    align-items: center;
    justify-content: space-between;

    border-top: black solid 1px;
    background: #f3f2f2;
}

.start {
    display: flex;
    align-items: flex-start;
}

.start .item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin: 10px 100px 10px 10px;
}

.end {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 10px;
}
